<template>
  <div class="layout" :class="{'layout-hide-text': spanLeft === spanLeftFold}">
    <grid-row>
      <grid-col :span="spanLeft"  :style="style">
        <div v-on:mouseover="expandMenu" v-on:mouseleave="foldMenu">
          <d-menu :active-name="activeName" :theme="theme" width="auto">
            <div class="layout-logo-left"></div>
            <d-menu-item name="user" icon="users" color="#98D358" :size="iconSize" :to="`/${ curLang }/setting/user`">
              <span class="layout-text">{{ langConfig.user }}</span>
            </d-menu-item>

            <d-menu-item name="userToken" icon="box" color="#7FB3D5" :size="iconSize" :to="`/${ curLang }/setting/userToken`">
              <span class="layout-text">{{ langConfig.userToken}}</span>
            </d-menu-item>
            <d-menu-item name="dataCenter" icon="radio" color="#FFD306" :size="iconSize" :to="`/${ curLang }/setting/dataCenter`">
              <span class="layout-text">{{ langConfig.dataCenter }}</span>
            </d-menu-item>
            <d-menu-item name="config" icon="edit" color="#7FB3D5" :size="iconSize" :to="`/${ curLang }/setting/config`">
              <span class="layout-text">{{ langConfig.config }}</span>
            </d-menu-item>
            <d-menu-item name="hosts" icon="monitor" color="#E67E22" :size="iconSize" :to="`/${ curLang }/setting/hosts`">
              <span class="layout-text">{{ langConfig.hosts }}</span>
            </d-menu-item>
            <d-menu-item name="brokerGroup" icon="layers" color="#58D68D" :size="iconSize" :to="`/${ curLang }/setting/brokerGroup`">
              <span class="layout-text">{{ langConfig.brokerGroup }}</span>
            </d-menu-item>
            <d-menu-item name="broker" icon="cpu" color="#F5B7B1" :size="iconSize" :to="`/${ curLang }/setting/broker`">
              <span class="layout-text">{{ langConfig.broker }}</span>
            </d-menu-item>
            <d-menu-item name="auditRole" icon="user" color="#9393FF" :size="iconSize" :to="`/${ curLang }/setting/auditRole`">
              <span class="layout-text">{{ langConfig.auditRole }}</span>
            </d-menu-item>
            <d-menu-item name="auditFlow" icon="sliders" color="#BB8FCE" :size="iconSize" :to="`/${ curLang }/setting/auditFlow`">
              <span class="layout-text">{{ langConfig.auditFlow }}</span>
            </d-menu-item>
            <d-menu-item name="task" icon="command" color="#9D9D9D" :size="iconSize" :to="`/${ curLang }/setting/task`">
              <span class="layout-text">{{ langConfig.task }}</span>
            </d-menu-item>
            <d-menu-item name="executor" icon="server" color="#ffaad5" :size="iconSize" :to="`/${ curLang }/setting/executor`">
              <span class="layout-text">{{ langConfig.executor }}</span>
            </d-menu-item>
            <d-menu-item name="alarm" icon="bell" color="#CA6F1E" :size="iconSize" :to="`/${ curLang }/setting/alarm`">
              <span class="layout-text">{{ langConfig.alarm }}</span>
            </d-menu-item>
            <d-menu-item name="namespace" icon="feather" color="#73C6B6" :size="iconSize" :to="`/${ curLang }/setting/namespace`">
              <span class="layout-text">{{ langConfig.namespace }}</span>
            </d-menu-item>
            <d-menu-item name="label" icon="tag" color="#5DADE2" :size="iconSize" :to="`/${ curLang }/setting/label`">
              <span class="layout-text">{{ langConfig.label }}</span>
            </d-menu-item>
          </d-menu>
        </div>
      </grid-col>
      <grid-col :span="spanRight">
        <div class="layout-header">
          <d-button type="borderless" icon="menu" class="toggle-btn" @click="toggleClick"></d-button>
          <div class="layout-breadcrumb">
            <d-breadcrumb>
              <d-breadcrumb-item href="#">{{langHeaderConfig.setting}}</d-breadcrumb-item>
              <d-breadcrumb-item>{{submenu}}</d-breadcrumb-item>
            </d-breadcrumb>
          </div>
        </div>
        <div class="layout-content">
          <router-view></router-view>
        </div>
      </grid-col>
    </grid-row>
  </div>
</template>

<script>
// import compoLang from '../../i18n/components.json';
import submenu from '../../mixins/submenu.js'

export default {
  components: {},
  mixins: [ submenu ],
  data () {
    return {
      menu: 'setting'
    }
  },
  mounted () {
    // this.submenu = compoLang[this.curLang][this.menu]['user'];
    // this.activeName = 'user';
  },
  computed: {
    curLang () {
      return this.$i18n.locale
    }
  }
}

</script>

<style scoped>
  .layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
  }
  .layout-logo-left{
    width: 90%;
    height: 0px;
    line-height: 20px;
    color: #ffffff;
    background: #ffffff;
    border-radius: 3px;
    margin: 15px auto;
    text-align: center;
  }
  .toggle-btn{
    margin-left: 15px;
  }
  .layout-header {
    padding-top: 10px;
    padding-left: 20px;
    box-sizing: border-box;
    flex-shrink: 0;
    /*background: #ffffff;*/
  }
  .layout-breadcrumb{
    padding: 10px 15px 0;
    display: inline-block;
  }
  .layout-content{
    padding-top: 20px;
    padding-bottom: 15px;
    overflow: hidden;
    /*background: #ffffff;*/
    border-radius: 4px;
    height: calc(100% - 50px);
  }
  .layout-hide-text .layout-text {
    display: none;
  }
  .dui-menu-dark.dui-menu-vertical .dui-menu-item-active:not(.dui-menu-submenu) {
    background: rgb(84, 92, 100);
  }
</style>



